<template>
    <div>
        <el-form
            v-loading="loading"
            element-loading-text="..."
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)"
            v-bind:rules="rules"
            ref="loginForm"
            :model="loginForm"
            class="loginContainer">
            <h3 class="loginTitle"></h3>
            <el-form-item prop="username">
                <el-input size="normal" type="text" v-model="loginForm.username" auto-complete="off"
                          aria-placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="password">
                <el-input size="normal" type="text" v-model="loginForm.password" @keydown.enter.native="submitLogin"
                          auto-complete="off" aria-placeholder=""></el-input>
            </el-form-item>
            <!--           -->
            <el-checkbox size="normal" class="loginRemember" v-model="checked"></el-checkbox>
            <el-button size="normal" type="primary" style="width:100%" @click="submitLogin"></el-button>
        </el-form>
    </div>
</template>

<script>


export default {
    name: "Login",
    data() {
        return {
            loading: false,
            loginForm: {
                username: 'admin',
                password: '123'
            },
            checked: true,
            // ,
            rules: {
                username: [{required: true, message: '', trigger: 'blur'}],
                password: [{required: true, message: '', trigger: 'blur'}]
            },

        }
    },
    methods: {
        submitLogin() {
            this.loading = true;
            // 
            this.$refs.loginForm.validate((valid) => {
                if (valid) {
                    this.postKeyValueRequest("/doLogin", this.loginForm).then(resp => {
                        this.loading = false;
                        if (resp) {
                            this.$store.commit('initCurrentHr', resp.data);
                            // session
                            window.sessionStorage.setItem("user", JSON.stringify(resp.data));
                            // push()
                            let path = this.$route.query.redirect;  // 
                            // 
                            this.$router.replace((path == '/' || path == undefined) ? '/home' : path);
                        }
                    })
                } else {
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'error'
                    });
                    return false;
                }
            });
        }
    }
}
</script>

<style scoped>
.loginContainer {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6; /**/
}

.loginTitle {
    margin: 20px auto 20px auto;
    text-align: center;
    color: #505458;
}

.loginRemember {
    text-align: left;
    margin: 0px 0px 15px 0px;
}
</style>
